<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentPlayList: Array,
    currentSongPlayId: Object,
  },

  mounted: function() {
    // console.log(this.$refs.canvas);
    // this.canvas = this.$refs.canvas
  },
  computed: {
    percentage: function() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function(n) {
      // console.log(n);
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);

      // context.beginPath();
      // context.arc(20, 20, 18, (Math.PI / 180) * 0, (Math.PI / 180) * 360);
      // context.strokeStyle = "lightgray";
      // context.lineWidth = 1;
      // context.stroke();

      // context.beginPath();
      // context.arc(20, 20, 18, (Math.PI / 180) * 0, (Math.PI / 180) * 360 * n);
      // context.strokeStyle = "#d43c33";
      // context.lineWidth = 1;
      // context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        16,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      context.lineWidth = 1;
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        16,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "gray";
      context.lineWidth = 1;
      context.stroke();
    },
  },
};
</script>

