<template>
  <div class="search" @scroll="scroll">
    <div class="searchbox">
      <div class="bxsearch">
        <input
          type="text"
          placeholder="搜索歌曲，歌手，专辑"
          v-model.trim="value"
          @keyup.enter="value && (searching = true)"
          @focus="changefocus"
          @blur="inputing = false"
        />
      </div>
    </div>

    <section class="hotsearch" v-if="!value && !searching">
      <h5>热门搜索</h5>
      <ul>
        <li
          v-for="hot in hots"
          :key="hot.first"
          @click="
            searching = true;
            value = hot.first;
          "
        >
          {{ hot.first }}
        </li>
      </ul>
      <ol class="historylist">
        <li
          class="history"
          v-for="(h, index) in history"
          :key="index"
          @click="
            searching = true;
            value = h;
          "
        >
          <div class="left">
            <i class="clock"></i>
            <span class="hname">{{ h }}</span>
          </div>
          <div class="right">
            <i class="del" @click.stop="del(index)"></i>
          </div>
        </li>
      </ol>
    </section>

    <section class="suggestpage" v-if="value && !searching">
      <h5 @click="searching = true">搜索:“{{ value }}”</h5>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
        >
          {{ item.keyword }}
        </li>
      </ul>
    </section>

    <section class="suggests" v-if="searching">
      <!-- <h5>搜索结果</h5> -->
      <ul>
        <Result
          v-for="(item, index) in searchResults"
          :key="index"
          :item="item"
          :currentSongId="currentSongId"
          :currentSong="currentSong"
          :playing="playing"
          @change-current-song="
            $emit('change-current-song', $event);
            $emit('change-current-play-list', searchResults);
          "
          :currentSongPlayId="currentSongPlayId"
        ></Result>
      </ul>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import Result from "@/components/Result.vue";
export default {
  components: {
    Result,
  },
  props: {
    playing: Boolean,
    currentSongId: Number,
    showPlayList: Boolean,
    currentPlayList: Array,
    currentSong: Object,
    currentSongPlayId: Object,
  },
  data: function() {
    return {
      hots: [],
      suggests: [],
      searchResults: [],
      value: "",
      searching: false,
      inputing: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
    };
  },
  methods: {
    changeCurrentSong: function(song) {
      // console.log(song);
      this.currentSong = song;
    },
    del(i) {
      this.history.splice(i, 1);
      // window.localStorage.removeItem(e);
    },
    changefocus: function() {
      this.inputing = true;
      this.searching = false;
      if (!this.searching) {
        this.axios
          .get("/search/suggest", {
            params: {
              keywords: this.value,
              type: "mobile",
            },
          })
          .then((res) => {
            if (this.value) {
              this.suggests = res.data.result.allMatch;
            }
          });
      } else {
        this.suggests = [];
      }
    },
    scroll: function(event) {
      // console.log(event);
      if (this.hasMore) {
        if (
          event.target.offsetHeight + event.target.scrollTop ===
          event.target.scrollHeight
        ) {
          // console.log("触底");

          this.getSearch();
        }
      } else {
        // console.log("没有更多了");
      }

      //   document.querySelector(".search").scrollHeight;
      //   document.querySelector(".search").scrollTop;
      //   document.querySelector(".search").offsetHeight;
    },
    getSearch: function() {
      this.axios
        .get("/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.page * 30,
          },
        })
        .then((res) => {
          // console.log(res);
          this.searchResults.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      // 记录一下
      // this.history.push(this.value)
      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
  created: function() {
    this.axios.get("/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },
  watch: {
    value: function(n) {
      if (this.inputing) {
        this.searching = false;
      }
      if (n && !this.searching) {
        this.axios
          .get("/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
      }
    },

    searching: function(n) {
      if (n && this.value) {
        this.getSearch();
      } else {
        this.searchResults = [];
      }
    },
    history: function(n) {
      this.history = n;
      console.log(n);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
};
</script>

